.task {
    text-align: left;
    margin: 10px;
    padding: 5px;
    border: 1px solid #000000;
}

p, button {
    display: inline-flex;
    align-content: center;
}

button.V {
    text-align: center;
    width: 25px;
    height: 25px;
    float: right;
    color: white;
    background-color: green;
}

button.X {
    text-align: center;
    width: 25px;
    height: 25px;
    float: right;
    color: white;
    background-color: red;
}